<template>
  <el-container>
    <el-header height="40" class="header">
      <el-row>
        <el-col :span="18">八维研修学院</el-col>
        <el-col :span="6" class="username">用户名：{{username}}</el-col>
      </el-row>
    </el-header>
    <el-header class="sub-header">
      <el-row class="subject">
        <el-col :span="18" class="subject-name">第一题：通配符匹配</el-col>
        <el-col :span="6" class="left-time">剩余时间：{{currentTime}} 分</el-col>
      </el-row>
    </el-header>
    <el-main>
      <el-row class="hp100">
        <el-col :span="8" class="hp100 q-r">
          <question :q="qlist[qindex]"></question>
        </el-col>
        <el-col :span="16" class="hp100">
          <answer @answer="saveAnswer" :a="alist[qindex]"></answer>
        </el-col>
      </el-row>
    </el-main>
    <el-footer>
      <el-row>
        <el-col :span="2">
          <el-dropdown trigger="click">
            <el-button class="el-dropdown-link" size="small">题目列表</el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="(q,i) in qlist" :key="i">
                <span
                  @click="qindex=i"
                  class="qlist-item"
                >{{q.exam_name}}：{{q.questions_stem.slice(0,8)}}...</span>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
        <el-col :span="4" :push="2">
          <el-button size="small" @click="prev">上一题</el-button>
          <el-button size="small" @click="next">下一题</el-button>
        </el-col>
        <el-col :span="2" :push="16">
          <el-button size="small" type="primary" :disabled="canSubmit" @click="submitAnswer">提交</el-button>
        </el-col>
      </el-row>
    </el-footer>
  </el-container>
</template>

<script>
import answer from "@/components/answer";
import question from "@/components/question";
export default {
  name: "index",
  data: function() {
    return {
      currentTime: new Date().toLocaleTimeString(),
      qlist: [],
      qindex: 0,
      username:'无信息',
      alist:[],
      exam_exam_id:null,
      starttime:'',
      endtime:'',
      canSubmit:false
    };
  },
  computed: {},
  components: {
    answer,
    question
  },
  methods: {
    prev() {
      this.qindex -= 1;
      if (this.qindex < 0) {
        this.qindex = 4;
      }
    },
    next() {
      this.qindex += 1;
      if (this.qindex > 4) {
        this.qindex = 0;
      }
    },
    unfinished(){
      return new Promise((resolve,reject)=>{
          let notanswer = this.alist.filter(item=>!item.student_answer);
          if(notanswer.length>0){
            this.$confirm(`还有${notanswer.length}道题未作答，确定交卷吗？`, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              resolve('ok')
            }).catch(() => {
              resolve('no')  
            });
          }else{
            resolve('ok')
          }
          
      })
    },
    async submitAnswer(){
      let isGoAhead = await this.unfinished()
      if(isGoAhead === 'no') return;
      let collectData = {
        "exam_exam_id":this.exam_exam_id,
        "questions":this.alist,
        "start_time":this.starttime*1,
        "end_time":this.endtime*1
      }
      this.canSubmit = true;
      let res;
      try { 
        res = await this.$http.post(`/exam/student`,collectData)
      }catch(e){
        this.$message({
          showClose: true,
          message: `something bad happened, please try again.`,
          type: "error"
        })
        this.canSubmit = false;
      }
      
      this.$alert(res.msg, '提示', {
          confirmButtonText: '确定',
          callback: action => {
            console.log(action)
          }
      });
      return false
    },
    saveAnswer(e){
      this.alist = this.alist.map(item=>{
        if(item.questions_id==e[1]){
          item.student_answer = e[0]
        }
        return item
      })
    },
    timer(lefttime){
      return new Promise(()=>{
        let timer = setInterval(() => {
          this.currentTime = new Date().toLocaleTimeString()
            //lefttime--;
            // if(lefttime < 0){
            //   this.currentTime = "时间已到，考试结束";
            //   console.log(this.currentTime)
            //   clearInterval(timer)
            // }
            // this.currentTime = Math.floor(lefttime/60)
          }, 1000);
      })
    }
  },
  async mounted() {
    
    let res = await this.$http.get(`/student/info`);
    if (res.code == 1) {
      this.username = res.data.student_name;
      this.exam_exam_id = res.data.exam_exam_id;
      this.starttime = res.data.start_time;
      this.endtime = res.data.end_time;
      // let lefttime = (this.endtime-this.starttime)/1000;
      this.timer();

      let qs = await this.$http.get(`/exam/unstart/${res.data.exam_exam_id}`);

      if (qs.code === 1) {
        this.qlist = qs.data.questions;
        this.alist = qs.data.questions;
      } else {
        this.$message({
          showClose: true,
          message: `${qs.msg}，请重试`,
          type: "error"
        });
      }

      
    } else {
      this.$message({
        showClose: true,
        message: `${res.msg}，请重试`,
        type: "error"
      });
    }
  }
};
</script>

