<template>
    <div class="answer-wrapper hp100">
        <el-row class="answer-bar">
            <el-col :span="20">
              答题框：
            </el-col>
            <el-col :span="4">
                <el-select size="small" v-model="codetype" @change="languageChange" placeholder="请选择">
                <el-option
                    v-for="item in codetypelist"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                    :disabled="item.disabled">
                </el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row class="answer-box">
          <el-col class="hp100">
            <codemirror
                :value="code"
                :options="editorOption"
                ref="myEditor"
                @change="codeChange">
            ></codemirror>
          </el-col>
        </el-row>
        
    </div>
</template>

<script>
import 'codemirror/mode/javascript/javascript'
import 'codemirror/mode/css/css'
import 'codemirror/mode/htmlmixed/htmlmixed'
import 'codemirror/mode/jsx/jsx'
import 'codemirror/mode/vue/vue'
import { codemirror } from 'vue-codemirror-lite'

export default {
    name:'answer',
    components:{
      codemirror
    },
    props:{
      a:{
        type: Object,
        default: function(){
          return {}
        }
      }
    },
    data () {
      return {
        code: '//请输入你的答案',
        editorOption:{
          mode: 'javascript',
          lineWrapping: true,
          lineNumbers: true
        },
        codetypelist: [
          {
            value: 'javascript',
            label: 'JavaScript'
          }, {
            value: 'html',
            label: 'HTML',
            disabled: false
          }, {
            value: 'css',
            label: 'CSS'
          }, {
            value: 'vue',
            label: 'VUE'
          }, {
            value: 'jsx',
            label: 'React'
          }
        ],
        codetype: 'JavaScript'
      }
    },
    computed: {
      editor() {
        // get current editor object
        return this.$refs.myEditor.editor
      }
    },
    watch:{
      a(_new,_old){
        //student_answer
        console.log('a',_new.student_answer)
        this.code = _new.student_answer || '请输入你的答案';
      }
    },
    methods:{
      codeChange(answer){
        this.$emit('answer',[answer,this.a.questions_id])
      },
      languageChange(item){
        this.editor.setOption('mode',item)
        
      }
    },
    mounted() {
      // use editor object...
      this.editor.focus()
      this.editor.setSize('100%','100%')
    }
}
</script>

<style scoped>
  .answer-wrapper{
    line-height: 32px;
    padding: 10px;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
  }
  .vue-codemirror-wrap{
    border: 1px solid #ccc;
    line-height: 1.6;
    height: 100%;
  }
  .CodeMirror {
  border: 1px solid #eee;
  height: auto;
}
  .answer-bar {
      line-height: 39px;
  }
  .answer-box{
    flex: 1;
  }
  .el-row:last-child {
      margin-bottom: 0;
    }
</style>
